import './App.css';

import React, { Component } from 'react'
import NavBar from './components/NavBar';
import News from './components/News';
import {
  BrowserRouter as Router,
  Switch,
  Route,
} from "react-router-dom";

export default class App extends Component {
  render() {
    return (
      <div>
        <Router>
          <NavBar/>

          <Switch>
            <Route exact path="/" key="general" ><News country = "in" category = "general" /></Route>
            <Route exact path="/entertainment"  key="entertainment" ><News country = "in" category = "entertainment" /></Route>
            <Route key="business" exact path="/business"><News country = "in" category = "business" /></Route>
            <Route key="health" exact path="/health"><News country = "in" category = "health" /></Route>
            <Route key="sports" exact path="/sports"><News country = "in" category = "sports" /></Route>
            <Route key="science" exact path="/science"><News country = "in" category = "science" /></Route>
            <Route key="technology" exact path="/technology"><News country = "in" category = "technology" /></Route>
          </Switch>

        </Router>
      </div>
    )
  }
}
