import React, { Component } from "react";
import NewsItem from "./NewsItem";
import Spinner from "./Spinner";
import PropTypes from 'prop-types'


export class News extends Component {

    static defaultProps = {
        country:'in',
        category:'business'
    }

    static propTypes = {
        country:PropTypes.string,
        category : PropTypes.string
    }

    constructor() {
        super();

        this.state = {
            articles: [],
            loading: false,
            page: 1,
            totalResults:1
        };
    }

    async componentDidMount() {
        let url =
            `https://newsapi.org/v2/top-headlines?country=${this.props.country}&category=${this.props.category}&apiKey=bd8daf232a9d43d4becaf184494f3293&page=1&pageSize=6`;
        
        
        this.setState({loading:true})
        
        let data = await fetch(url);
        let parsedData = await data.json();

        this.setState({
            articles: parsedData.articles,
            totalResults : parsedData.totalResults,
            loading:false
        });
    }

    handlePrevClick = async () => {

        let url =
            `https://newsapi.org/v2/top-headlines?country=${this.props.country}&category=${this.props.category}&apiKey=bd8daf232a9d43d4becaf184494f3293&page=${this.state.page - 1 > 0 ? this.state.page - 1 : 1}&pageSize=6`;
        
        this.setState({loading : true});
        
        let data = await fetch(url);
        let parsedData = await data.json()

        this.setState({
            articles: parsedData.articles,
            loading: false,
            page: this.state.page - 1 > 0 ? this.state.page - 1 : 1
        });
    }

    handleNextClick = async () => {

                let url =
                `https://newsapi.org/v2/top-headlines?country=${this.props.country}&category=${this.props.category}&apiKey=bd8daf232a9d43d4becaf184494f3293&page=${this.state.page + 1}&pageSize=6`;
                
                this.setState({loading:true});
                
                let data = await fetch(url);
                let parsedData = await data.json()

                this.setState({
                    articles: parsedData.articles,
                    loading: false,
                    page: this.state.page + 1
                });       

    }

    render() {
        return (
            <div className="container my-3">
                <h1>Top headlines</h1>

                {this.state.loading && <Spinner/>}

                <div className="row">
                    { !this.state.loading && this.state.articles.map((element) => {
                        return (
                            <div className="col-md-4" key={element.url}>
                                <NewsItem
                                    title={element.title}
                                    imageUrl={element.urlToImage}
                                    description={element.description}
                                    newsUrl={element.url}
                                />
                            </div>
                        );
                    })}
                </div>

                <div className="container d-flex justify-content-between my-5">
                    <button
                        disabled={this.state.page <= 1}
                        onClick={this.handlePrevClick}
                        type="button"
                        className="btn btn-primary"
                    >
                        &larr;Previous page
                    </button>

                    <button
                        type="button"
                        disabled = {this.state.page >= Math.ceil(this.state.totalResults/6)}
                        onClick={this.handleNextClick}
                        className="btn btn-primary"
                    >
                        Next page&rarr;
                    </button>
                </div>
            </div>
        );
    }
}

export default News;
